import React, { useState, useEffect } from "react";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import { authFetch } from "auth";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import { makeStyles } from "@material-ui/core/styles";
import "assets/css/App.css";
import {
  FormControl,
  FormHelperText,
  InputLabel,
  Select,
  MenuItem,
  Grid,
} from "@material-ui/core";

const styles = (theme) => ({
  select: {
    color: "white",
    "&:before": {
      borderColor: "white",
      color: "white",
    },
    "&:after": {
      borderColor: "white",
    },
  },
  icon: {
    fill: "white",
  },
});

const useStyles = makeStyles(styles);

export default function FormDialog({ handleSnackbar, setEspece, espece }) {
  const [bassinsList, setBassinsList] = useState("");
  const [open, setOpen] = useState(false);
  const [bassin, setBassin] = useState("");
  const [date, setDate] = useState("");

  const classes = useStyles();

  useEffect(() => {
    var url = "/api/bassins";
    authFetch(url)
      .then((response) => {
        if (response.status === 401) {
          handleSnackbar("danger", "Sorry you aren't authorized!");
          return null;
        } else if (response.status === 500) {
          handleSnackbar("warning", "Bad request");
          return null;
        }
        return response.json();
      })
      .then((response) => {
        setBassinsList(response.bassins);
        setBassin(response.bassins[0]);
      });
  }, [handleSnackbar]);

  const handleAddClose = () => {
    setOpen(false);
    var url = "/api/cycles";
    const requestOptions = {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ Bassin: bassin, Date: date }),
    };
    authFetch(url, requestOptions)
      .then((response) => {
        if (response.status === 401) {
          return null;
        } else if (response.status === 500) {
          return null;
        }
        return response.json();
      })
      .then((response) => {
        response !== null
          ? handleSnackbar("success", response.message)
          : handleSnackbar("warning", "Bad request");
      });
  };

  return (
    <div>
      <FormControl
        style={{
          backgroundColor: "#f50057",
          color: "fff",
          boxShadow:
            "0px 3px 1px -2px rgb(0 0 0 / 20%), 0px 2px 2px 0px rgb(0 0 0 / 14%), 0px 1px 5px 0px rgb(0 0 0 / 12%)",
          borderRadius: "4px",
          marginRight: "8px",
          padding: 8,
        }}
      >
        <Select
          className={classes.select}
          inputProps={{
            classes: {
              icon: classes.icon,
            },
          }}
          labelId="espece-label"
          id="espece-select"
          value={espece}
          onChange={(e) => setEspece(e.target.value)}
        >
          <MenuItem value="Tout">Tout</MenuItem>
          <MenuItem value="Tilapia">Tilapia</MenuItem>
          <MenuItem value="Crevettes">Crevettes</MenuItem>
          <MenuItem value="Carpes">Carpes</MenuItem>
        </Select>
        <FormHelperText style={{ color: "white" }}>
          Filtrer par espèces
        </FormHelperText>
      </FormControl>
      <Button
        id="ButtonDialog"
        variant="contained"
        color="secondary"
        onClick={() => setOpen(true)}
      >
        Add Cycle
      </Button>
      <Dialog
        open={open}
        onClose={() => {
          setOpen(false);
          setBassin("");
          setDate("");
        }}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">Ajouter un cycle</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Veuillez choisir le Bassin et la date de remplissage.
          </DialogContentText>
          <Grid container spacing={1}>
            <Grid item>
              <FormControl>
                <InputLabel id="bassin-label">Bassin</InputLabel>
                <Select
                  labelId="bassin-label"
                  id="bassin-select"
                  value={bassin}
                  onChange={(e) => setBassin(e.target.value)}
                >
                  {bassinsList !== ""
                    ? bassinsList.map((Bassin) => (
                        <MenuItem value={Bassin}>{Bassin}</MenuItem>
                      ))
                    : null}
                </Select>
                <FormHelperText>Sélectionner un bassin</FormHelperText>
              </FormControl>
            </Grid>
            <Grid item>
              <TextField
                required
                id="data_rempli"
                label="Date Remplissage"
                type="date"
                defaultValue={new Date().toISOString().substring(0, 10)}
                variant="filled"
                onChange={(e) => setDate(e.target.value)}
              />
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={() => {
              setOpen(false);
              setBassin("");
              setDate("");
            }}
            color="primary"
          >
            Annuler
          </Button>
          <Button onClick={handleAddClose} color="primary">
            Ajouter
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
