import React, { useState, useEffect } from "react";

import { authFetch } from "auth";
import { Grid, Button, TextField } from "@material-ui/core";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import Snackbar from "components/Snackbar/Snackbar.js";

export default function Alimentation() {
  const [tc, setTC] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("primary");

  const [cyclesList, setCyclesList] = useState(undefined);
  const [cycle, setCycle] = useState(undefined);

  const [poidsReel, setPoidsReel] = useState(undefined);

  useEffect(() => {
    var url = "/api/alimentation";
    authFetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        console.log(response.cycles);
        setCyclesList(
          response.cycles.sort((a, b) => (a.Bassin > b.Bassin ? 1 : -1))
        );
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  }, []);

  useEffect(() => {
    cycle
      ? cycle.ali
        ? setPoidsReel(cycle.Poids)
        : setPoidsReel(cycle["poids aliment a donner"])
      : console.log("nop");
  }, [cycle]);

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setColor("");
      setMessage("");
    }, 4000);
  };

  const handleConfirm = () => {
    let temp = cyclesList;
    temp[cycle.id] = {
      ...temp[cycle.id],
      ali: true,
      Poids: poidsReel,
      maj: true,
    };
    setCyclesList(temp);
    var url = "/api/alimentation";
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ bassin: cycle.Bassin, poids: poidsReel }),
    };
    authFetch(url, requestOptions)
      .then((response) => {
        if (response.status === 401) {
          return null;
        } else if (response.status === 500) {
          return null;
        }
        return response.json();
      })
      .then((response) => {
        response !== null
          ? handleSnackbar("success", response.message)
          : handleSnackbar("warning", "Bad request");
      });
  };

  return (
    <div>
      <Snackbar
        place="tc"
        color={color}
        message={message}
        open={tc}
        closeNotification={() => setTC(false)}
        close
      />
      <Grid container spacing={2}>
        <Grid
          container
          spacing={1}
          item
          xs={4}
          style={{ overflow: "scroll", height: "100vh" }}
        >
          {cyclesList &&
            cyclesList.map((c, key) => (
              <Grid item xs={12}>
                <Button
                  style={{
                    height: "80px",
                    color: "white",
                    backgroundColor: c.ali ? "#4caf50" : "#3f51b5",
                  }}
                  id={key}
                  variant="contained"
                  fullWidth
                  onClick={(e) => {
                    e.target.id === ""
                      ? setCycle({
                          ...cyclesList[e.target.offsetParent.id],
                          id: e.target.offsetParent.id,
                        })
                      : setCycle({
                          ...cyclesList[e.target.id],
                          id: e.target.id,
                        });
                  }}
                >
                  {c.Bassin}
                </Button>
              </Grid>
            ))}
        </Grid>
        <Grid item xs={8}>
          {cycle ? (
            <Card>
              <CardHeader color="rose">
                <h1>{cycle.Bassin}</h1>
              </CardHeader>
              <CardBody>
                <Grid container spacing={2} alignItems="center">
                  <Grid item xs={3}>
                    <Card style={{ marginBottom: "10px" }}>
                      <h4 style={{ textAlign: "center" }}>
                        Poids à donner: {cycle["poids aliment a donner"]}
                      </h4>
                      <p>Aliment: {cycle.Type_Aliment}</p>
                    </Card>
                  </Grid>
                  <Grid item xs={6} style={{ textAlign: "center" }}>
                    <TextField
                      variant="outlined"
                      label="Alimentation réelle"
                      value={poidsReel}
                      onChange={(e) => setPoidsReel(e.target.value)}
                    ></TextField>
                  </Grid>
                  <Grid item xs={3} alignItems="center">
                    <Button
                      onClick={() => handleConfirm()}
                      variant="contained"
                      color="primary"
                    >
                      Confirmer
                    </Button>
                  </Grid>
                </Grid>
              </CardBody>
            </Card>
          ) : null}
        </Grid>
      </Grid>
    </div>
  );
}
