import React, { useState, useEffect } from "react";
import { authFetch } from "auth";
import Button from "@material-ui/core/Button";
import {
  Grid,
  FormControl,
  Select,
  InputLabel,
  MenuItem,
  FormHelperText,
  Card,
  CardHeader,
  Typography,
} from "@material-ui/core";
import Snackbar from "components/Snackbar/Snackbar.js";
import CustomTableEdit from "./TableEdit";
import CardBody from "./Card/CardBody";

export default function Lots({ cycle_, resetCycle }) {
  const [cycle, setCycle] = useState(cycle_.toString());
  const [lot, setLot] = useState();
  const [lotsList, setLotsList] = useState([]);
  const [lotsMenu, setLotsMenu] = useState([]);
  const [tc, setTC] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("primary");
  const [data, setData] = useState({ peches: [], semis: [], statistiques: [] });
  const [espece, setEspece] = useState("Tilapia");

  useEffect(() => {
    var url = "/api/lots";
    authFetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        setLotsList(response.lotsList);
        setLotsMenu([]);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  }, [cycle]);

  useEffect(() => {
    let temp = [];
    for (const lots of lotsList) {
      if (lots[1].toString() === cycle.toString()) {
        setLot(lots[2]);
        temp = [lots[2], ...temp];
      }
    }
    setLotsMenu(temp);
  }, [lotsList, cycle]);

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setColor("");
      setMessage("");
    }, 4000);
  };

  useEffect(() => {
    var url = "/api/lotData/" + lot;
    authFetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        setEspece(response.espece);
        setData({
          peches: response.peches,
          semis: response.semis,
          statistiques: response.statistiques,
        });
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  }, [lot]);

  return (
    <div id="lot">
      <Snackbar
        place="tc"
        color={color}
        message={message}
        open={tc}
        closeNotification={() => setTC(false)}
        close
      />
      <Grid container spacing={1} justify="center">
        <Grid item xs={2} align="center">
          <Typography variant="h4">{espece}</Typography>
        </Grid>
        <Grid item xs={4} align="center">
          <FormControl>
            <InputLabel id="cycle-label">Cycle</InputLabel>
            <Select
              labelId="cycle-label"
              id="cycle-select"
              value={cycle}
              onChange={(e) => setCycle(e.target.value)}
            >
              {lotsList.map((Cycle) => (
                <MenuItem value={Cycle[1]}>
                  {Cycle[0] + "#" + Cycle[1]}
                </MenuItem>
              ))}
            </Select>
            <FormHelperText>Sélectionner la table à afficher</FormHelperText>
          </FormControl>
        </Grid>
        <Grid container spacing={1} item xs={4} align="center" justify="center">
          {lotsMenu.map((Lot) => (
            <Grid item xs={4}>
              <Button
                variant="contained"
                color="secondary"
                onClick={() => setLot(Lot)}
              >
                Lot #{Lot}
              </Button>
            </Grid>
          ))}
          <Grid item xs={4}>
            <Button variant="contained" color="primary" onClick={resetCycle}>
              Back
            </Button>
          </Grid>
        </Grid>
        <Grid item xs={6} container align="center">
          {/* Semis */}
          <Card>
            <CardHeader id="Header" color="secondary" title="Semis" />
            <CardBody style={{ padding: 0 }}>
              <CustomTableEdit
                tableHead={[
                  "date",
                  "quantite",
                  "poids",
                  "quantitelb",
                  "commentaire",
                ]}
                tableHeaderColor="primary"
                tableData={data.semis}
                postUrl={"/api/lotData/Semis"}
                newLine={true}
                info_for_post={lot}
              />
            </CardBody>
          </Card>
        </Grid>
        <Grid item xs={6} container align="center">
          {/* Statistiques */}
          <Card>
            <CardHeader id="Header" color="info" title="Statistiques" />
            <CardBody style={{ padding: 0 }}>
              <CustomTableEdit
                tableHead={["date", "typestat", "quantitelb", "commentaire"]}
                tableHeaderColor="primary"
                tableData={data.statistiques}
                postUrl={"/api/lotData/Statistiques"}
                newLine={true}
                info_for_post={lot}
              />
            </CardBody>
          </Card>
        </Grid>
        <Grid item xs={6} container align="center">
          {/* Peches */}
          <Card>
            <CardHeader id="Header" color="primary" title="Pêches" />
            <CardBody style={{ padding: 0 }}>
              <CustomTableEdit
                tableHead={[
                  "date",
                  "quantite",
                  "poids",
                  "quantitelb",
                  "commentaire",
                  "destination",
                ]}
                tableHeaderColor="primary"
                tableData={data.peches}
                postUrl={"/api/lotData/Peches"}
                newLine={true}
                info_for_post={lot}
              />
            </CardBody>
          </Card>
        </Grid>
        <Grid item xs={6} container align="center">
          {/* Peches */}
        </Grid>
      </Grid>
    </div>
  );
}
